.ImageGalleryItem {
    border-radius: 2px;
  }
  
.ImageGalleryItem-image {
    width: 100%;
    height: 260px;
    object-fit: cover;
    border-radius: 4px;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.ImageGalleryItem-image:hover {
    transform: scale(1.03);
    cursor: zoom-in;
}